@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(36,36,36);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{width:100vw;min-height:calc(94vh - 50px)}

.header{display:flex;width:100vw;height:50px;position:fixed;top:0;left:0;z-index:5;background-color:#242424;justify-content:space-between;border-bottom:1px solid #505050}.header__flags{display:flex;margin-left:10px}.header__flags__flag{height:20px;width:35px;margin:0 5px;align-self:center}.header__flags__flag:hover{cursor:pointer;box-shadow:0 0 8px #888888}.header__menubar{display:flex;font-family:'Courier Prime', monospace}.header__menubar__menu{height:50px;line-height:50px;text-align:center;width:15vw;transition:.3s}.header__menubar__menu:hover{cursor:pointer;color:#effeff}a{color:#505050;text-decoration:none}@media (max-width: 800px){.header__menubar__menu img{height:30px;margin-top:10px;-webkit-filter:invert(0.6);filter:invert(0.6)}.header__menubar__menu:hover{-webkit-filter:invert(1);filter:invert(1)}}

.switch-wrapper{position:relative}.switch-wrapper>div{position:absolute}

.about{width:100vw;margin-top:50px;margin-bottom:6vh}.about__intro{font-family:'Courier Prime', monospace;text-align:center;padding-top:30px}.about__intro__pic{width:20vw;height:20vw;border-radius:50%;box-shadow:0 0 8px #888888;-webkit-filter:brightness(70%);filter:brightness(70%)}.about__intro__name{font-size:3em;font-weight:600;color:#aaa}.about__intro__profession{font-size:1.5em;color:#505050;font-style:italic}.about__content{box-sizing:border-box;width:55vw;margin-left:25vw;padding:20px;display:flex;font-family:'Courier Prime', monospace}.about__content__text{width:70%;padding:10px}.about__content__text__title{text-align:left;font-size:24px;font-weight:600;color:#aaa;padding:20px 0}.about__content__text__description{font-size:16px;color:#c8c8c8;text-align:justify;padding-right:15px}.about__content__contact{width:30%;padding:10px}.about__content__contact__title{text-align:left;font-size:24px;font-weight:600;color:#aaa;padding:20px 0}.about__content__contact__description{color:#c8c8c8;display:flex;flex-direction:column}.about__content__contact__description__info{display:flex;align-items:center;transition:.3s}.about__content__contact__description__info img{width:30px;height:30px;padding:5px;margin-right:10px;-webkit-filter:invert(0.8);filter:invert(0.8)}.about__content__contact__description__info:hover{cursor:pointer;color:#fff}.about__content__contact__description__info:hover img{-webkit-filter:invert(1);filter:invert(1)}span.fontcolor{color:#242424}@media (max-width: 800px){.about__intro__name{width:80%;margin-left:10%;font-size:2em}.about__intro__profession{font-size:1.2em}.about__content{flex-direction:column;margin-left:20vw}.about__content__text{width:100%}.about__content__text__description{padding-right:0}.about__content__contact{width:100%}}

.technologies{width:100vw;margin-top:50px;margin-bottom:6vh;font-family:'Courier Prime', monospace;color:#aaa;font-size:16px}.technologies__title{text-align:center;padding-top:30px;font-size:3em;font-weight:600;padding-bottom:50px}.technologies__container{width:60vw;margin-left:20vw;display:flex;flex-wrap:wrap}.technologies__container span{display:block;text-align:center;font-size:16px;line-height:16px;padding:10px;box-sizing:border-box;width:20vw;transition:.3s}.technologies__container span:hover{color:#effeff;cursor:crosshair}.progressBar{width:80%;margin-left:10%;height:10px;margin-top:5px;box-sizing:border-box;border:1px solid #aaa}.fillBar{height:100%;width:1%;background-color:#8c8c8c}@media (max-width: 800px){.technologies__title{font-size:2em}.technologies__container{width:80vw;margin-left:10vw;flex-direction:column;padding-bottom:20px}.technologies__container span{width:60%;margin-left:20%}}

.projects{width:100vw;margin-top:50px;margin-bottom:6vh;font-family:'Courier Prime', monospace;color:#aaa;font-size:16px}.projects__title{text-align:center;padding-top:30px;font-size:3em;font-weight:600;padding-bottom:50px}.projects__container{width:60vw;margin-left:20vw;display:flex;flex-direction:column}.projects__container__project{display:flex;align-items:center;padding:40px 20px;box-sizing:border-box;border-bottom:1px solid #505050}.projects__container__project:last-child{border-bottom:none}.projects__container__project__pic{width:30vw;-webkit-filter:brightness(50%);filter:brightness(50%);transition:.3s}.projects__container__project__pic:hover{cursor:pointer}.projects__container__project__description{padding:20px}.projects__container__project__description span{display:block;font-weight:600;font-size:20px;padding-bottom:5px}.projects__container__project:hover{color:#c8c8c8}.projects__container__project:hover .projects__container__project__pic{-webkit-filter:brightness(100%);filter:brightness(100%)}@media (max-width: 800px){.projects__title{font-size:2em;padding-bottom:20px}.projects__container{width:80vw;margin-left:10vw}.projects__container__project{flex-direction:column;text-align:center}.projects__container__project__pic{width:70vw;-webkit-filter:brightness(100%);filter:brightness(100%)}}

.footer{height:5vh;position:fixed;bottom:0;left:0;z-index:5;width:100vw;text-align:center;line-height:5vh;font-size:10px;font-family:'Courier Prime', monospace;border-top:1px solid #505050;color:#effeff;background-color:#242424}

